/**
 * @file App.jsx
 *   .
 */

import { useState, useEffect } from 'react';
import { useMediaQuery } from 'react-responsive';

// Import services
import { firebase } from './services';

// Import components
import GameRoom from './components/GameRoom';
import Lobby from './components/Lobby';

/**
 *  : Firebase ,         .
 */
function App() {
  const [db, setDb] = useState(null);
  const [user, setUser] = useState(null);
  const [gameId, setGameId] = useState(null);
  const isMobile = useMediaQuery({ maxWidth: 768 });

  //    Firebase     
  useEffect(() => {
    // Firebase    
    const { auth, db: dbInstance } = firebase.initializeFirebase();
    setDb(dbInstance);
    
    //     
    const unsubscribe = firebase.onAuthStateChangedListener(auth, (currentUser) => {
      if (currentUser) setUser(currentUser);
      else firebase.signInAnonymouslyWithFirebase(auth).catch(error => console.error("  :", error));
    });
    
    return () => unsubscribe();
  }, []);

  //  
  if (!user || !db) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <p className="text-xl text-gray-700"> ...</p>
        </div>
      </div>
    );
  }

  //  ID      
  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-100 to-gray-200 dark:from-gray-800 dark:to-gray-900 text-gray-900 dark:text-gray-100">
      <div className={`container mx-auto px-4 ${isMobile ? 'py-4' : 'py-8'} max-w-7xl`}>
        <header className="mb-6">
          <h1 className="text-3xl md:text-4xl font-bold text-center text-indigo-800 dark:text-indigo-400">  ( )</h1>
          <p className="text-center text-gray-600 dark:text-gray-400 mt-2"> ID: {user.uid.substring(0, 8)}...</p>
          <div className="border-b border-gray-300 dark:border-gray-700 mt-4"></div>
        </header>
        <main>
          {gameId ? 
            <GameRoom db={db} user={user} gameId={gameId} setGameId={setGameId} /> : 
            <Lobby db={db} user={user} setGameId={setGameId} isMobile={isMobile} />
          }
        </main>
      </div>
    </div>
  );
}

export default App;