/**
 * Firebase : Firebase     .
 *        .
 */
import { initializeApp } from 'firebase/app';
import { getAuth, signInAnonymously, onAuthStateChanged } from 'firebase/auth';
import {
  getFirestore,
  doc,
  updateDoc,
  onSnapshot,
  collection,
  addDoc,
  getDoc,
  arrayUnion,
  writeBatch,
  increment,
  enableIndexedDbPersistence
} from 'firebase/firestore';

//       
let isFirebaseInitialized = false;
let firebaseApp = null;
let firestoreDb = null;
let firebaseAuth = null;
let connectionStatus = 'disconnected'; // 'connected', 'disconnected', 'connecting', 'error'
let lastError = null;
let retryCount = 0;
const MAX_RETRY_COUNT = 5;
const RETRY_DELAY_MS = 3000; // 3  

/**
 * Firebase   
 * :          .
 */
const firebaseConfig = {
  apiKey: 'AIzaSyBNJtmpRWzjobrY556bnHkwbZmpFJqgPX8',
  authDomain: 'text-adventure-game-cb731.firebaseapp.com',
  projectId: 'text-adventure-game-cb731',
  storageBucket: 'text-adventure-game-cb731.appspot.com',
  messagingSenderId: '1092941614820',
  appId: '1:1092941614820:web:5545f36014b73c268026f1',
  measurementId: 'G-FNGF42T1FP',
};

/**
 * Firebase   .
 * @returns {Object}   
 */
export const getConnectionStatus = () => {
  return {
    status: connectionStatus,
    error: lastError,
    retryCount,
    isInitialized: isFirebaseInitialized
  };
};

/**
 * Firebase   .
 *     .
 * @returns {Object} Firebase , , DB   
 */
export const initializeFirebase = () => {
  if (isFirebaseInitialized && firebaseApp && firestoreDb && firebaseAuth) {
    return { app: firebaseApp, auth: firebaseAuth, db: firestoreDb };
  }

  try {
    connectionStatus = 'connecting';
    console.log('Firebase   ...');
    
    // Firebase  
    firebaseApp = initializeApp(firebaseConfig);
    firebaseAuth = getAuth(firebaseApp);
    firestoreDb = getFirestore(firebaseApp);
    
    //    IndexedDB  
    enableIndexedDbPersistence(firestoreDb)
      .then(() => {
        console.log('  .');
      })
      .catch((err) => {
        if (err.code === 'failed-precondition') {
          console.warn('        .');
        } else if (err.code === 'unimplemented') {
          console.warn('     .');
        }
      });
    
    isFirebaseInitialized = true;
    connectionStatus = 'connected';
    retryCount = 0;
    lastError = null;
    console.log('Firebase  ');
    
    return { app: firebaseApp, auth: firebaseAuth, db: firestoreDb };
  } catch (error) {
    connectionStatus = 'error';
    lastError = error;
    console.error('Firebase  :', error);
    
    //      
    if (retryCount < MAX_RETRY_COUNT) {
      retryCount++;
      console.log(`${retryCount}   (${RETRY_DELAY_MS}ms )...`);
      
      setTimeout(() => {
        initializeFirebase();
      }, RETRY_DELAY_MS);
    }
    
    //    (  )
    return { app: null, auth: null, db: null, error };
  }
};

/**
 * Firebase   .
 * @param {Object} app - Firebase  
 * @returns {Object} Firebase  
 */
export const getFirebaseAuth = (app) => {
  if (!app) {
    console.warn(' Firebase   .');
    return null;
  }
  return getAuth(app);
};

/**
 * Firebase Firestore  .
 * @param {Object} app - Firebase  
 * @returns {Object} Firebase Firestore 
 */
export const getFirebaseFirestore = (app) => {
  if (!app) {
    console.warn(' Firebase   .');
    return null;
  }
  return getFirestore(app);
};

/**
 *   .
 *    .
 * @param {Object} auth - Firebase  
 * @returns {Promise<Object>}  
 */
export const signInAnonymouslyWithFirebase = async (auth) => {
  if (!auth) {
    console.warn(' Firebase   .');
    return Promise.reject(new Error(' Firebase   .'));
  }
  
  let attempts = 0;
  const maxAttempts = 3;
  
  const attemptSignIn = async () => {
    try {
      attempts++;
      return await signInAnonymously(auth);
    } catch (error) {
      console.error(`   ( ${attempts}/${maxAttempts}):`, error);
      
      if (attempts < maxAttempts) {
        console.log(`${attempts}   ...`);
        //   (1, 2, 4...)
        const delay = Math.pow(2, attempts - 1) * 1000;
        await new Promise(resolve => setTimeout(resolve, delay));
        return attemptSignIn();
      }
      
      throw error;
    }
  };
  
  return attemptSignIn();
};

/**
 *      .
 * @param {Object} auth - Firebase  
 * @param {Function} callback -       
 * @returns {Function}   
 */
export const onAuthStateChangedListener = (auth, callback) => {
  if (!auth) {
    console.warn(' Firebase   .');
    return () => {}; //   
  }
  return onAuthStateChanged(auth, callback);
};

// Firestore  
export {
  doc,
  updateDoc,
  onSnapshot,
  collection,
  addDoc,
  getDoc,
  arrayUnion,
  writeBatch,
  increment
};

export default {
  initializeFirebase,
  getFirebaseAuth,
  getFirebaseFirestore,
  signInAnonymouslyWithFirebase,
  onAuthStateChangedListener,
  firebaseConfig
};