// :    API      .
//         .
const GROQ_API_KEY = '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';

//   
let apiStatus = {
  lastCallTime: null,
  lastCallSuccess: true,
  failureCount: 0,
  inCooldown: false,
  cooldownUntil: null
};

//   (  )
const responseCache = new Map();
const CACHE_MAX_SIZE = 50;
const CACHE_TTL_MS = 24 * 60 * 60 * 1000; // 24

//      (API   )
const DEFAULT_EVENTS = [
  {
    title: "   ",
    description: "       .",
    effects: [
      { nation: "", effect: "", value: -30 },
      { nation: "", effect: "", value: -5 }
    ]
  },
  {
    title: " ",
    description: "     .",
    effects: [
      { nation: "", effect: "", value: 50 },
      { nation: "", effect: "", value: 50 }
    ]
  },
  {
    title: " ",
    description: "    .",
    effects: [
      { nation: "", effect: "", value: 1, tech_name: "engineering" },
      { nation: "", effect: "", value: 1, tech_name: "agriculture" }
    ]
  },
  {
    title: " ",
    description: "    .",
    effects: [
      { nation: "", effect: "", value: -3 },
      { nation: "", effect: "", value: -3 }
    ]
  },
  {
    title: " ",
    description: "     .",
    effects: [
      { nation: "", effect: "", value: 10 },
      { nation: "", effect: "", value: 10 }
    ]
  }
];

//     (API   )
const DEFAULT_ADVISOR_RESPONSES = {
  "": [
    { action: "build_military", value: 10, explanation: "10  ." },
    { action: "attack", from: "", to: "", explanation: "  ." }
  ],
  "": [
    { action: "research", tech_name: "agriculture", explanation: "  ." },
    { action: "research", tech_name: "engineering", explanation: "  ." }
  ],
  "": [
    { action: "research", tech_name: "diplomacy", explanation: "  ." },
    { action: "move_troops", from: "", to: "", value: 5, explanation: "  5  ." }
  ],
  "": [
    { action: "research", tech_name: "espionage", explanation: "  ." },
    { action: "build_military", value: 5, explanation: "5  ." }
  ]
};

/**
 *   .
 * @param {string} prompt -  
 * @param {string} systemPrompt -  
 * @returns {string}  
 */
const createCacheKey = (prompt, systemPrompt) => {
  return `${systemPrompt}:${prompt}`;
};

/**
 *   .
 * @param {string} key -  
 * @returns {Object|null}    null
 */
const getFromCache = (key) => {
  if (!responseCache.has(key)) return null;
  
  const cachedItem = responseCache.get(key);
  const now = Date.now();
  
  //  TTL 
  if (now - cachedItem.timestamp > CACHE_TTL_MS) {
    responseCache.delete(key);
    return null;
  }
  
  return cachedItem.data;
};

/**
 *   .
 * @param {string} key -  
 * @param {Object} data -  
 */
const saveToCache = (key, data) => {
  //    
  if (responseCache.size >= CACHE_MAX_SIZE) {
    //    
    const oldestKey = [...responseCache.entries()]
      .sort((a, b) => a[1].timestamp - b[1].timestamp)[0][0];
    responseCache.delete(oldestKey);
  }
  
  responseCache.set(key, {
    data,
    timestamp: Date.now()
  });
};

/**
 *        .
 * @param {Object} gameData -  
 * @returns {Object}  
 */
const generateLocalEventResponse = (gameData) => {
  //     
  let nationNames = [];
  try {
    if (gameData && gameData.nations) {
      nationNames = Object.keys(gameData.nations);
    }
  } catch (e) {
    console.warn('      :', e);
  }
  
  //      
  const randomEvent = { ...DEFAULT_EVENTS[Math.floor(Math.random() * DEFAULT_EVENTS.length)] };
  
  //           
  if (nationNames.length > 0) {
    randomEvent.effects = randomEvent.effects.map(effect => {
      const randomNation = nationNames[Math.floor(Math.random() * nationNames.length)];
      return { ...effect, nation: randomNation };
    });
  }
  
  return randomEvent;
};

/**
 *       .
 * @param {string} advisorType -  
 * @param {string} command -  
 * @param {Object} gameData -  
 * @returns {Object}   
 */
const generateLocalAdvisorResponse = (advisorType, command, gameData) => {
  //         
  const responses = DEFAULT_ADVISOR_RESPONSES[advisorType] || DEFAULT_ADVISOR_RESPONSES[""];
  let response = { ...responses[Math.floor(Math.random() * responses.length)] };
  
  //       
  try {
    if (gameData && gameData.map && gameData.map.territories) {
      const territories = Object.values(gameData.map.territories);
      const myTerritories = territories.filter(t => t.owner === gameData.currentNation);
      const enemyTerritories = territories.filter(t => t.owner !== gameData.currentNation && t.owner !== null);
      
      //           
      if (response.action === 'attack' && myTerritories.length > 0 && enemyTerritories.length > 0) {
        response.from = myTerritories[0].name;
        response.to = enemyTerritories[0].name;
        response.explanation = `${response.from} ${response.to} .`;
      } else if (response.action === 'move_troops' && myTerritories.length >= 2) {
        response.from = myTerritories[0].name;
        response.to = myTerritories[1].name;
        response.explanation = `${response.from} ${response.to} ${response.value}  .`;
      }
    }
  } catch (e) {
    console.warn('      :', e);
  }
  
  //         
  if (command.includes('') || command.includes('')) {
    response = { ...DEFAULT_ADVISOR_RESPONSES[""][0] };
  } else if (command.includes('') || command.includes('')) {
    response = { ...DEFAULT_ADVISOR_RESPONSES[""][0] };
  } else if (command.includes('') || command.includes('')) {
    response = { ...DEFAULT_ADVISOR_RESPONSES[""][1] };
  }
  
  return response;
};

/**
 * API     .
 * @returns {boolean} API   
 */
const checkApiStatus = () => {
  const now = Date.now();
  
  //   
  if (apiStatus.inCooldown && now < apiStatus.cooldownUntil) {
    return false;
  }
  
  //  
  if (apiStatus.inCooldown && now >= apiStatus.cooldownUntil) {
    apiStatus.inCooldown = false;
    apiStatus.failureCount = 0;
  }
  
  return true;
};

/**
 * API   .
 */
const recordApiSuccess = () => {
  apiStatus.lastCallTime = Date.now();
  apiStatus.lastCallSuccess = true;
  apiStatus.failureCount = 0;
  apiStatus.inCooldown = false;
};

/**
 * API      .
 */
const recordApiFailure = () => {
  apiStatus.lastCallTime = Date.now();
  apiStatus.lastCallSuccess = false;
  apiStatus.failureCount++;
  
  //      
  if (apiStatus.failureCount >= 3) {
    apiStatus.inCooldown = true;
    //      ( 5)
    const cooldownTime = Math.min(Math.pow(2, apiStatus.failureCount - 3) * 30000, 300000);
    apiStatus.cooldownUntil = Date.now() + cooldownTime;
    console.warn(`API   ${cooldownTime / 1000}   `);
  }
};

/**
 * Groq API  LLM  .
 * API      .
 * @param {string} prompt -  
 * @param {string} systemPrompt -  
 * @param {string} model -   (: "llama-3.1-405b-reasoning")
 * @param {Object} gameData -   ( )
 * @returns {Promise<Object>} - LLM  
 */
export const callGroqLlmApi = async (prompt, systemPrompt, model = "llama-3.1-405b-reasoning", gameData = null) => {
  const cacheKey = createCacheKey(prompt, systemPrompt);
  const cachedResponse = getFromCache(cacheKey);
  
  //    
  if (cachedResponse) {
    console.log('  :', cacheKey);
    return cachedResponse;
  }
  
  // API   (    )
  if (!checkApiStatus()) {
    console.warn('API  ,   ');
    return handleLocalFallback(prompt, systemPrompt, gameData);
  }
  
  const url = 'https://api.groq.com/openai/v1/chat/completions';
  const isJsonRequest = systemPrompt.includes('JSON');
  const payload = {
    model,
    messages: [{ role: 'system', content: systemPrompt }, { role: 'user', content: prompt }],
    temperature: 0.8,
    max_tokens: 1024,
    ...(isJsonRequest && { response_format: { type: 'json_object' } }),
  };
  
  try {
    console.log('Groq API  ...');
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 10000); // 10 
    
    const response = await fetch(url, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${GROQ_API_KEY}` },
      body: JSON.stringify(payload),
      signal: controller.signal
    });
    
    clearTimeout(timeoutId);
    
    if (!response.ok) {
      console.error(`Groq API   (: ${response.status})`);
      recordApiFailure();
      return handleLocalFallback(prompt, systemPrompt, gameData);
    }
    
    const result = await response.json();
    const llmOutputText = result.choices?.[0]?.message?.content || '{}';
    let parsedResponse;
    
    if (isJsonRequest) {
      try {
        parsedResponse = JSON.parse(llmOutputText);
      } catch (parseError) {
        console.error("JSON  :", parseError);
        recordApiFailure();
        return handleLocalFallback(prompt, systemPrompt, gameData);
      }
    } else {
      parsedResponse = { content: llmOutputText };
    }
    
    //      
    saveToCache(cacheKey, parsedResponse);
    recordApiSuccess();
    return parsedResponse;
    
  } catch (error) {
    console.error("Groq API   :", error);
    recordApiFailure();
    return handleLocalFallback(prompt, systemPrompt, gameData);
  }
};

/**
 * API      .
 * @param {string} prompt -  
 * @param {string} systemPrompt -  
 * @param {Object} gameData -  
 * @returns {Object}   
 */
const handleLocalFallback = (prompt, systemPrompt, gameData) => {
  console.log('    ...');
  
  //    
  if (systemPrompt.includes('') && systemPrompt.includes(' ')) {
    const localEvent = generateLocalEventResponse(gameData);
    console.log('  :', localEvent.title);
    return localEvent;
  }
  
  //    
  if (systemPrompt.includes('  ')) {
    //    
    let advisorType = ""; // 
    try {
      const advisorMatch = systemPrompt.match(/ '([^']+)'/);
      if (advisorMatch && advisorMatch[1]) {
        if (advisorMatch[1] === '') advisorType = "";
        else if (advisorMatch[1] === '') advisorType = "";
        else if (advisorMatch[1] === '') advisorType = "";
        else if (advisorMatch[1] === '') advisorType = "";
      }
    } catch (e) {
      console.warn('    :', e);
    }
    
    const localResponse = generateLocalAdvisorResponse(advisorType, prompt, gameData);
    console.log('   :', localResponse.action);
    return localResponse;
  }
  
  //     
  return {
    content: "AI    .   .",
    isLocalFallback: true
  };
};

/**
 *  API   .
 * @returns {Object} API  
 */
export const getApiStatus = () => {
  return {
    ...apiStatus,
    cacheSize: responseCache.size,
    isAvailable: !apiStatus.inCooldown
  };
};

export default {
  callGroqLlmApi,
  getApiStatus
};