/**
 * @file useResponsive.js
 *      .
 */

import { useMediaQuery } from 'react-responsive';

/**
 *          
 * 
 * @returns {Object}     boolean 
 */
const useResponsive = () => {
  // Tailwind CSS    
  const isMobile = useMediaQuery({ maxWidth: 639 }); // xs: < 640px
  const isTablet = useMediaQuery({ minWidth: 640, maxWidth: 767 }); // sm: >= 640px
  const isLaptop = useMediaQuery({ minWidth: 768, maxWidth: 1023 }); // md: >= 768px
  const isDesktop = useMediaQuery({ minWidth: 1024, maxWidth: 1279 }); // lg: >= 1024px
  const isLargeDesktop = useMediaQuery({ minWidth: 1280 }); // xl: >= 1280px

  //      
  const isMinWidth = {
    sm: useMediaQuery({ minWidth: 640 }), // sm 
    md: useMediaQuery({ minWidth: 768 }), // md 
    lg: useMediaQuery({ minWidth: 1024 }), // lg 
    xl: useMediaQuery({ minWidth: 1280 }), // xl 
  };

  //      
  const isMaxWidth = {
    xs: useMediaQuery({ maxWidth: 639 }), // xs 
    sm: useMediaQuery({ maxWidth: 767 }), // sm 
    md: useMediaQuery({ maxWidth: 1023 }), // md 
    lg: useMediaQuery({ maxWidth: 1279 }), // lg 
  };

  //      
  const deviceType = isMobile 
    ? 'mobile' 
    : isTablet 
      ? 'tablet' 
      : isLaptop 
        ? 'laptop' 
        : isDesktop 
          ? 'desktop' 
          : 'largeDesktop';

  return {
    isMobile,
    isTablet,
    isLaptop,
    isDesktop,
    isLargeDesktop,
    isMinWidth,
    isMaxWidth,
    deviceType
  };
};

export { useResponsive };